
:root {
    --headbar-height: 64px;
    --box-width: calc(100% - 10px * 2);
    --box-height: calc(100vh - 64px - 10px);
    --box-radius: 15px;
    --mode: 'unknown';
    --tab-height: 63px;
    --app-height: calc(100vh - var(--tab-height));
    /* --bg-url: url(https://bing.rthe.net/wallpaper); */
    /* --bg-url: url(https://picsum.photos/1920/1080); */
    --bg-url: url(https://source.unsplash.com/random/1920x1080);
    --bg-color: #f6f7f9;
    --elem-color: #ffffff;
    --first-assist-color: #eee;
    --main-color: #000000;
    --body-color: #f1f3f4;
    --inactive-color: #888;
    --search-padding: 500px;
    --blur-color: var(--blur-color-light);
    --blur-color-dark: #2e2e2ecc;
    --blur-color-light: #ffffffcc;
    --blur-color-solid: rgba(240, 240, 240, 0.8);
    --blur-color-dark-solid: #333333aa;
    --blur-color-light-solid: #f0f0f0cc;
    --shadow-color: var(--shadow-color-light);
    --shadow-color-light: rgba(29, 29, 29, 0.219);
    --shadow-color-dark: rgba(29, 29, 29, 0.411);
    --accent-color: rgb(17, 148, 235);
    --accent-color-opa: rgb(17, 148, 235, .6);
}
@media screen and (prefers-color-scheme: light) {
:root {
        --mode: 'light';
        --bg-color: #dddddd;
        --elem-color: #ffffff;
        --first-assist-color: #eee;
        --main-color: #000000;
        --main-color-revert: #ffffff;
        --main-color-assist: #424242;
        --main-color-opa: #00000023;
        --body-color: #f1f3f4;
        --inactive-color: #888;
        --blur-color: var(--blur-color-light);
        --blur-color-solid: var(--blur-color-light-solid);
        --shadow-color: var(--shadow-color-light);
        --accent-color: rgb(17, 148, 235);
        --accent-color-opa: rgb(17, 148, 235, .6);
}
.bg-enable {
        background: none !important;
}
}
@media screen and (prefers-color-scheme: dark) {
:root {
        --mode: 'dark';
        --bg-color: #17181a;
        --elem-color: #2d2e30;
        --first-assist-color: #595a5e;
        --main-color: #ffffff;
        --main-color-assist: #afafaf;
        --main-color-opa: #ffffff23;
        --body-color: #1b1b1d;
        /* --body-color: #005663; */
        --inactive-color: #ddd;
        --blur-color: var(--blur-color-dark);
        --blur-color-solid: var(--blur-color-dark-solid);
        --shadow-color: var(--shadow-color-dark);
        --accent-color: rgb(17, 148, 235);
        --accent-color-opa: rgb(17, 148, 235, .6);
}
.bg-enable {
        /* background: #00000058 !important; */
        background: radial-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%), radial-gradient(rgba(0, 0, 0, 0) 33%, rgba(0, 0, 0, 0.3) 166%) !important;
}
.el-switch__core {
        /* margin: 0; */
        /* position: relative; */
        /* width: 40px; */
        /* height: 20px; */
        border: 1px solid #ffffff00;
        /* outline: 0; */
        /* border-radius: 10px; */
        /* -webkit-box-sizing: border-box; */
        /* box-sizing: border-box; */
        background: #27282a;
        /* -webkit-transition: border-color .3s, background-color .3s; */
        /* vertical-align: middle; */
        /* transition: border-color .3s, background-color .3s; */
}
    /* img {
        opacity: .8;
    } */
}
@media screen and (min-width: 10px) and (max-width: 600px) {
:root {
        --tab-height: 0px;
}
